}

.description__triangle {
  position: absolute;
  width: 0;
  height: 0;
  border-top: 4px solid transparent;
  border-left: 5px solid #a7d2db;
  border-bottom: 4px solid transparent;
  top: 4px;
  right: -13px;
}

.description__text {
  font-family: 'PFDinDisplayPro-Thin', sans-serif;
